import utils from 'src/utils'

const ActivityManage = {
  getJoininfo (id) { // 
    return this.$ajax({
      method: 'post',
      url: '/frontend/user/join-info',
      params: {
        activityId: id
      }
    })
  },
  subScribe (data) { // 
    return utils.ajax({
      method: 'post',
      url: '/frontend/user/order-activity',
      params: data
    })
  },
  getUserinfo (data) { // 
    return utils.ajax({
      method: 'get',
      url: '/user/consumer-user/info',
      params: data
    })
  },
  getWebinarinfo (data) { // 
    return utils.ajax({
      method: 'post',
      url: '/frontend/live/info',
      params: data
    })
  },
  getShareInfo (data) { // 
    return utils.ajax({
      method: 'post',
      url: '/frontend/share/info',
      params: data
    })
  },
  async getRegactivity (data) { // 
    const promsObj = await new Promise((resolve, reject) => {
      utils.ajax({
        method: 'get',
        url: '/frontend/user/reg-activity',
        params: data
      }).then(res => {
        resolve(res)
      }).catch(error => {
        reject(error)
      })
    })
    return promsObj
  },
  getForm (data) { // 
    return utils.ajax({
      method: 'post',
      url: '/frontend/question/info',
      params: data
    })
  },
  saveForm (data) { // 
    return utils.ajax({
      method: 'post',
      url: '/frontend/user/apply-activity',
      params: data
    })
  },
  queryPassSdkInfo () {
    return utils.ajax({
      method: 'post',
      url: '/common/paas/create-access-token',
      params: {}
    })
  },
  /*  */
  queryOnlineNum (data) {
    return utils.ajax({
      method: 'post',
      url: '/frontend/channel/get-online-num',
      params: data
    })
  },
  getShareSign (url) { // tokenappid
    return utils.ajax({
      method: 'get',
      url: '/common/wechat/get-share-sign?url=' + url
    })
  }
}
export default ActivityManage
